@import '_mixins';

$dark: #1A2930;
//$light: #C5C1C0;
$light: white;
$gold: #F7CE3E;
$light-text: white;
$dark-text: black;

/*** NAVBAR ***/
.top-navbar {
  width: 100%;
  position: absolute;
  top: 0px;
  z-index: 2;
  background-color: transparent;
}
/*** HERO ***/
.hero-img {
  width: 100%;
  height: 100%;
  margin: 0em 0em 2em 0em;
  font-weight: 900;
  text-shadow: 0px 0px 3px #000;
  @extend %flex-centered;
  @extend %shadow-layer;
}
/*** FOOTER ***/
.footer{
  width: 100%;
  padding: 2em 0em 2em 0em;
}

.footer-icon, .footer-icon:hover{
  color: inherit;
}
.footer-icon:hover {
  opacity: 0.7;
}
/*** PROJECTS ***/
.projects {
  width: 100%;
  padding: 2em 1em 2em 1em;
  background-color: $light;
}
/*** CONTACT ***/
.overlay-full-page {
  height: 100%;
  width: 100%;
  position: fixed;
  z-index: 10;
  left: 0;
  top: 0;
  background-color: $dark;
  overflow-x: hidden; // disable horizontale scroll
}
/*** GENERAL ***/
html, body, .wrapper {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  font-family: 'Raleway', Helvetica, sans-serif;
}
.flex-centered, %flex-centered {
  display: flex;
  align-items: center;
  justify-content: center;
}

.filter-layer {
  background-color: rgba($dark, 0.5);
  background-size: cover;
}

.shadow-layer, %shadow-layer {
  box-shadow: inset 0 0 0 1000px rgba(0,0,0,0.3);
}

.gray-filter {
  @include gray-filter(100%);
}

.bg-home {
  @include background(home);
}
.bg-portfolio {
  @include background(portfolio);
}
.bg-resume {
  @include background(resume);
}
.bg-contact {
  @include background(contact);
}
/*** WHOLE PAGE ***/
.whole-page {
  position:fixed;
  padding:0;
  margin:0;

  top:0;
  left:0;

  width: 100%;
  height: 100%;
  background:rgba(255,255,255,0.5);

}

/* ANIMATION */
.fade-animation {
  -webkit-transition: 1s;
  transition: 0.5s;
}

.fade-animation.ng-enter {
  opacity: 0;
}

.fade-animation.ng-leave {
  opacity: 1;
}

.fade-animation.ng-enter.ng-enter-active {
  opacity: 1;
}

.fade-animation.ng-leave.ng-leave-active {
  opacity: 0;
}

/* BLINKING CURSOR ANIMATION */
.blinking-cursor {

  -webkit-animation: 1s blink step-end infinite;
  -moz-animation: 1s blink step-end infinite;
  -ms-animation: 1s blink step-end infinite;
  -o-animation: 1s blink step-end infinite;
  animation: 1s blink step-end infinite;
}

@keyframes blink {
  from, to {
    color: transparent;
  }
  50% {
    color: white;
  }
}

@-moz-keyframes blink {
  from, to {
    color: transparent;
  }
  50% {
    color: white;
  }
}

@-webkit-keyframes blink {
  from, to {
    color: transparent;
  }
  50% {
    color: white;
  }
}

@-ms-keyframes blink {
  from, to {
    color: transparent;
  }
  50% {
    color: white;
  }
}

@-o-keyframes blink {
  from, to {
    color: transparent;
  }
  50% {
    color: white;
  }
}

/* MEDIA QUERY */
html {
  a, p {
      font-size: 1rem;
  }
  h1 {
      font-size: 3.5rem;
  }   
}

@include breakpoint(sm) {
  html {
    a, p {
        font-size: 1rem;
    }
    h1 {
        font-size: 3.5rem;
    }  
  }   
}
@include breakpoint(md) {
  html {
    a, p {
        font-size: 1.2rem;
    }
    h1 {
        font-size: 5rem;
    }
    .top-navbar .nav-links a {
      display: block;
    }  
    .top-navbar .nav-links .navbar-icon {
      display: none;
    }  
  }
}
@include breakpoint(lg) {
  html {
    a, p {
        font-size: 1.3rem;
    }
    h1 {
        font-size: 5rem;
    }  
  }
}
@include breakpoint(xl) {
  html {
    a, p {
        font-size: 1.4rem;
    }
    h1 {
        font-size: 5rem;
    }  
  }
}